export default {
	data() {
		return {
			status: 0,
			navbar: [
				{
					name: '未开始'
				},
				{
					name: '进行中'
				},
				{
					name: '完成未清账'
				},
				{
					name: '已完成'
				}
			],
			// 悬浮按钮
			left: 0,
			right: 10,
			bottom: 60,
			bgColor: '#5677fc',
			btnList: [
				{
					bgColor: '#64B532',
					text: '添加',
					fontSize: 28,
					color: '#fff'
				},
				{
					bgColor: '#16C2C2',
					text: '补单',
					fontSize: 28,
					color: '#fff'
				}
			],
			orderList: [],
			page: 1,
			limit: 20,
			operationModal: false,
			buttonList: [
				{
					text: '修改',
					type: 'green',
					plain: true
				},
				{
					text: '删除',
					type: 'red',
					plain: true
				},
				{
					text: '取消',
					plain: true
				}
			],
			id: undefined
		};
	},
	onLoad() {
		this.page = 1;
		this.orderList = [];
		this.getData();
	},
	methods: {
		showOperation: function(id) {
			this.operationModal = true;
			this.id = id;
		},
		hideOperation: function() {
			this.operationModal = false;
		},
		handleOperation: function(e) {
			let index = e.index;
			switch (index) {
				case 0:
					this.hideOperation();
					uni.navigateTo({
						url: '/pages/order/detail?type=edit&id=' + this.id
					});
					break;
				case 1:
					this.hideOperation();
					const db = wx.cloud.database({
						env: 'jnmc-ronxp'
					});
					// 删除订单图片
					db.collection('order')
						.doc(this.id)
						.field({ files: true })
						.get()
						.then(response => {
							const fileIDS = response.data.files.map(item => item.id);
							if (fileIDS && fileIDS.length > 0) {
								wx.cloud.deleteFile({
									fileList: fileIDS,
									success: res => {
										console.log('删除订单图片结果：' + res);
									},
									fail: err => {
										console.log(err);
									}
								});
							}
						}).catch(res => {
							console.log(res);
						});
					db.collection('order')
						.doc(this.id)
						.remove()
						.then(response => {
							if (response.stats.removed > 0) {
								this.tui.toast('删除订单成功');

								this.page = 1;
								this.orderList = [];
								this.getData();
							} else {
								this.tui.toast('删除订单失败');
							}
						})
						.catch(res => {
							console.log(res);
						});
					break;
				default:
					this.hideOperation();
					break;
			}
		},
		getData: function() {
			const db = wx.cloud.database({
				env: 'jnmc-ronxp'
			});
			db.collection('order')
				.field({
					community: true,
					building: true,
					unit: true
				})
				.where({
					status: this.status
				})
				.orderBy('create_time', 'desc')
				.skip((this.page - 1) * this.limit)
				.limit(this.limit)
				.get()
				.then(response => {
					this.orderList = response.data;
				});
		},
		change(e) {
			this.status = e.index;
			this.page = 1;
			this.orderList = [];
			this.getData();
		},
		gotoDetail: function(id) {
			uni.navigateTo({
				url: '/pages/order/detail?type=detail&id=' + id
			});
		},
		onClick(e) {
			let index = e.index;
			switch (index) {
				case 0:
					uni.navigateTo({
						url: '/pages/order/detail?type=add'
					});
					break;
				case 1:
					uni.navigateTo({
						url: '/pages/order/detail?type=fill'
					});
					break;
				default:
					break;
			}
		}
	}
};
